import "core-js/modules/es.regexp.to-string";
import "core-js/modules/es.string.replace";
import "core-js/modules/web.dom-collections.iterator";
import "core-js/modules/es.regexp.to-string";
import "core-js/modules/es.string.replace";
import "core-js/modules/web.dom-collections.iterator";
/**
 * Component: Free Gift script
 * ------------------------------------------------------------------------------
 * A snippet to display the cart value required to receive a free gift
 *
 * @namespace freeGift
 */
import * as cart from '@shopify/theme-cart';
import { formatMoney } from '@shopify/theme-currency';
import axios from 'axios';

/**
 * DOM selectors.
 */
const selectors = {
  message: '[js-free-gift="message"]'
};

/**
 * Create a new gift with purchase object.
 */
export default (() => {
  /**
   * DOM node selectors.
   */
  const nodeSelectors = {
    message: [...document.querySelectorAll(selectors.message)]
  };

  /**
   * Initialise component.
   */
  function init() {
    setEventBusListeners();
  }

  /**
   * Set listeners to update on every cart change.
   */
  function setEventBusListeners() {
    Frame.EventBus.listen('AjaxCart:updated', response => update(response));
  }

  /**
   * Update gift with purchase notification.
   * @param {Object} cartData - Cart Data.
   */
  function update(cartData) {
    render(cartData.total_price);
    handleGift(cartData);
    if (theme.features.currencyConverter) {
      Frame.EventBus.emit('Currency:convertAll');
    }
  }

  /**
   * Update notification template using new values.
   * @param {Number} total - Current cart total.
   */
  function render(total) {
    const minValue1 = theme.freeGift.minValue1;
    const difference1 = minValue1 - total;
    const messages = theme.freeGift.messages;
    if (total > 0) {
      if (total < minValue1) {
        /**
         * Message shown when customer has not spent enough to
         * earn the first gift
         */
        nodeSelectors.message.forEach(element => {
          element.innerHTML = messages.initial_partial.replace('#value#', createMessage(minValue1)).replace('%shop_currency%', '');
        });
      } else if (total >= minValue1) {
        /**
         * Message shown when customer has reached minValue1 is reached and
         * no second gift available
         */
        nodeSelectors.message.forEach(element => {
          element.innerHTML = messages.initial_ideal.replace('#value#', createMessage(minValue1)).replace('%shop_currency%', '');
        });
      }
    } else {
      /**
       * Message shown when customer has not spent anything
       */
      nodeSelectors.message.forEach(element => {
        element.innerHTML = messages.blank.replace('#value#', createMessage(minValue1)).replace('%shop_currency%', '');
      });
    }
  }

  /**
   * Checks whether a gift should be added or removed from the cart
   * @param {Object} cartData - cart data
   */
  function handleGift(cartData) {
    const total = cartData.total_price;
    const quo = getGiftQty(cartData);
    const minValue1 = theme.freeGift.minValue1;
    const giftsToAdd = [];
    let giftLineItemsInCart = checkGiftsInCart(cartData);
    giftLineItemsInCart;
    if (total <= 0 || total < minValue1) {
      if (giftLineItemsInCart.length) {
        removeGifts(giftLineItemsInCart);
      }
      return;
    }
    if (total >= minValue1) {
      giftsToAdd.push(1);
    }
    if (giftsToAdd && giftLineItemsInCart.length) {
      if (quo !== giftLineItemsInCart[0].quantity) {
        updateGifts(cartData);
      }
    } else {
      addGifts(cartData, giftsToAdd);
    }
  }

  /**
   * Checks how many gifts are present in the cart
   * @param {Object} cartData - cart data
   * @returns {Array} - array of gift line items in the cart.
   */
  function checkGiftsInCart(cartData) {
    const giftsInCart = [];
    const gift1LineItem = checkGiftAdded(cartData, 1);
    if (gift1LineItem) {
      giftsInCart.push(gift1LineItem);
    }
    return giftsInCart;
  }

  /**
   * Add multiple gifts to the cart at once.
   * @param {Object} cartData - cart data
   * @param {Array} giftsToAdd - Array of gifts to add to the cart
   */
  function addGifts(cartData, giftsToAdd) {
    const filteredGiftsToAdd = giftsToAdd.filter(gift => {
      return !checkGiftAdded(cartData, gift);
    });
    if (!filteredGiftsToAdd.length) {
      return;
    }
    const addItemsArray = filteredGiftsToAdd.map(gift => {
      const giftId = getItemId();
      const giftQty = getGiftQty(cartData);
      return {
        id: giftId,
        quantity: giftQty,
        properties: {
          'Free Gift': 'Yes',
          '_gift-product': gift
        }
      };
    });
    axios.post('/cart/add.js', {
      items: addItemsArray
    }).then(addResponses => {
      Frame.EventBus.emit('AjaxCart:freeGiftAdded', addResponses[addResponses.length - 1]);
      return addResponses;
    }).catch(error => {
      throw new Error('Could not add gifts to the cart', error);
    });
  }

  /**
  * Add multiple gifts to the cart at once.
  * @param {Object} cartData - cart data
  * @param {Array} giftsToAdd - Array of gifts to add to the cart
  */
  function updateGifts(cartData) {
    const giftId = getItemId();
    const giftQty = getGiftQty(cartData);
    var addItemsArray = {};
    addItemsArray[giftId] = giftQty;
    axios.post('/cart/update.js', {
      updates: addItemsArray
    }).then(addResponses => {
      Frame.EventBus.emit('AjaxCart:freeGiftAdded', addResponses[addResponses.length - 1]);
      return addResponses;
    }).catch(error => {
      throw new Error('Could not add gifts to the cart', error);
    });
  }

  /**
   * Removes the gift passed in as a param if it is present in the cart
   * @param {Object} cartData - cart data
   * @param {Number} gift
   * @returns response
   */
  function removeGift(cartData, gift) {
    try {
      const giftLineItem = checkGiftAdded(cartData, gift);
      if (!giftLineItem) {
        return;
      }
      const cartLineItemElement = document.querySelector(".ajax-cart__line-item[data-key=\"".concat(giftLineItem.key, "\"]"));
      Frame.EventBus.emit('AjaxCart:removingItem', cartLineItemElement);
      cart.removeItem(giftLineItem.key).then(response => {
        Frame.EventBus.emit('AjaxCart:itemRemoved', response);
        return response;
      }).catch(() => {
        throw new Error('could not remove gift from cart');
      });
    } catch (error) {
      throw new Error('Could not check if gift is in cart', error);
    }
  }

  /**
   * Remove multiple line items from the cart.
   * @param {Array} lineItemsToRemove - Array of line item objects to remove
   */
  function removeGifts(lineItemsToRemove) {
    const removeRequests = lineItemsToRemove.map(lineItem => {
      const cartLineItemElement = document.querySelector(".ajax-cart__line-item[data-key=\"".concat(lineItem.key, "\"]"));
      Frame.EventBus.emit('AjaxCart:removingItem', cartLineItemElement);
      return cart.removeItem(lineItem.key);
    });
    Promise.all(removeRequests).then(removeResponses => {
      Frame.EventBus.emit('AjaxCart:itemRemoved', removeResponses[removeResponses.length - 1]);
      return removeResponses;
    }).catch(error => {
      throw new Error('Could not remove gift from cart', error);
    });
  }

  /**
   * Gets the quantity based on item number
   * @param {Number} gift
   * @returns Number item qty
   */
  function getGiftQty(cartData) {
    const quo = Math.floor(cartData.total_price / theme.freeGift.minValue1);
    if (cartData.total_price > theme.freeGift.minValue1) {
      return quo;
    }
  }

  /**
   * Checks if the gift passed in as a param is present in the cart
   * @param {Object} cartData - cart data
   * @param {Number} gift
   * @returns {Object} line item
   */
  function checkGiftAdded(cartData, gift) {
    const giftId = theme.freeGift.itemId1;
    return cartData.items.find(item => {
      const matchingVariantId = item.variant_id === giftId;
      const hasGiftProperty = item.properties && item.properties['_gift-product'] === gift;
      return matchingVariantId && hasGiftProperty;
    });
  }

  /**
   * Generates a currency formatted value
   * @param {Number} value
   * @returns {String} value formatted into a currency
   */
  function createMessage(value) {
    return formatMoney(value, theme.moneyFormat);
  }

  /**
   * Get free gift id value.
   * @returns {Number}
   */
  function getItemId() {
    return theme.freeGift.itemId1;
  }

  /**
   * Validate that gift quantities are correct.
   * - Check the gift quantities are correct.
   * - Update cart and refresh page if either gift needs updating.
   */
  function validateCheckoutCart() {
    cart.getState().then(cartData => {
      const gift1LineItem = cartData.items.find(item => {
        return item.properties && item.properties['_gift-product'] === 1;
      });
      const gift2LineItem = cartData.items.find(item => {
        return item.properties && item.properties['_gift-product'] === 2;
      });
      if (!gift1LineItem && !gift2LineItem) {
        return;
      }
      const lineItemsToUpdate = [];
      var minValue1 = theme.freeGift.minValue1;
      minValue1 = minValue1.toString();
      minValue1 = minValue1.slice(0, -2);
      minValue1 = parseInt(minValue1);
      const quo = Math.floor(window.Shopify.Checkout.estimatedPrice / minValue1);
      if (gift1LineItem && gift1LineItem.quantity < quo || gift1LineItem && gift1LineItem.quantity > quo) {
        lineItemsToUpdate.push({
          key: gift1LineItem.key,
          quantity: quo
        });
      }
      if (gift2LineItem && gift2LineItem.quantity > theme.freeGift2.itemQty2) {
        lineItemsToUpdate.push({
          key: gift2LineItem.key,
          quantity: theme.freeGift2.itemQty2
        });
      }
      if (lineItemsToUpdate.length) {
        updateGiftLineItems(lineItemsToUpdate);
      }
    }).catch(error => {
      throw new Error('Could not get cart data', error);
    });
  }

  /**
      * Validate that gift quantities are correct.
      * - Check the gift quantities are correct.
      * - Update cart and refresh page if either gift needs updating.
      */
  function validateGiftc() {
    cart.getState().then(cartData => {
      const gift1LineItem = cartData.items.find(item => {
        return item.properties && item.properties['_gift-product'] === 1;
      });
      const gift2LineItem = cartData.items.find(item => {
        return item.properties && item.properties['_gift-product'] === 2;
      });
      if (!gift1LineItem && !gift2LineItem) {
        return;
      }
      const lineItemsToUpdate = [];
      var minValue1 = theme.freeGift.minValue1;
      minValue1 = minValue1.toString();
      minValue1 = minValue1.slice(0, -2);
      minValue1 = parseInt(minValue1);
      const quo = Math.floor(window.Shopify.Checkout.estimatedPrice / minValue1);
      if (gift1LineItem && gift1LineItem.quantity < quo || gift1LineItem && gift1LineItem.quantity > quo) {
        lineItemsToUpdate.push({
          key: gift1LineItem.key,
          quantity: quo
        });
      }
      if (gift2LineItem && gift2LineItem.quantity > theme.freeGift2.itemQty2) {
        lineItemsToUpdate.push({
          key: gift2LineItem.key,
          quantity: theme.freeGift2.itemQty2
        });
      }
      if (lineItemsToUpdate.length) {
        updateGiftLineItems(lineItemsToUpdate);
      }
    }).catch(error => {
      throw new Error('Could not get cart data', error);
    });
  }

  /**
   * Update gift line items.
   * - Reloads the page once request is finished.
   */
  function updateGiftLineItems(lineItemsToUpdate) {
    const updateRequests = lineItemsToUpdate.map(item => {
      return cart.updateItem(item.key, {
        quantity: item.quantity
      });
    });
    Promise.all(updateRequests).then(() => {
      window.location.reload();
    }).catch(error => {
      throw new Error('Could not update gift line items', error);
    });
  }

  /**
   * Expose public interface.
   */
  return Object.freeze({
    init,
    validateCheckoutCart
  });
});