import "core-js/modules/es.string.includes";
import "core-js/modules/es.string.replace";
import "core-js/modules/web.dom-collections.iterator";
import "core-js/modules/es.string.includes";
import "core-js/modules/es.string.replace";
import "core-js/modules/web.dom-collections.iterator";
/**
 * Section: Cart drawer
 * -----------------------------------------------------------------------------
 * Contains highly coupled scripts to render cart items onto a drawer.
 *
 * @namespace cartDrawer
 */
import * as cart from '@shopify/theme-cart';
import { formatMoney } from '@shopify/theme-currency';
import { getSizedImageUrl } from '@shopify/theme-images';
import { register } from '@shopify/theme-sections';
import { disableBodyScroll, enableBodyScroll } from 'body-scroll-lock';
import cssClasses from '~/helpers/cssClasses';
import { on, decodeHTMLEntities, handleize } from '~/helpers/utils';
import Toggle from '~/components/toggle';
import QuantitySelector from '~/components/quantity-selector';

/**
 * DOM selectors.
 */
const selectors = {
  body: '[js-cart-drawer="body"]',
  cartAddOn: '[js-cart-drawer="cartAddOn"]',
  container: '[js-cart-drawer="container"]',
  footer: '[js-cart-drawer="footer"]',
  lineItem: '[js-ajax-cart="lineItem"]',
  productJson: '[js-product-json]',
  subtotal: '[js-ajax-cart="subtotal"]',
  quantitySelector: '[js-ajax-cart="quantitySelector"]',
  productsLinks: '[js-ajax-cart="productLink"]'
};

/**
 * State selectors.
 */
const stateSelectors = {
  cartAddOn: 'has-cart-add-on'
};

/**
 * Attribute and data selectors.
 */
const dataSelectors = {
  ajaxCart: 'js-ajax-cart'
};

/**
 * Create a new drawer instance.
 */
const Drawer = Toggle({
  toggleSelector: 'cartDrawer'
});

/**
 * Instance globals.
 */
let quantitySelectorTimer = {};

/**
 * Register the `cart-drawer` section type.
 */
register('cart-drawer', {
  /**
   * Initialise section.
   */
  init() {
    Drawer.init();
    this.setNodeSelectors();
    this.setRemoveEvents(this.container);
    this.constructQuantitySelectors();
    this.dataLayerLinks();
  },
  /**
   * Set cached node selectors.
   */
  setNodeSelectors() {
    this.body = document.querySelector(selectors.body);
    this.container = document.querySelector(selectors.container);
    this.footer = document.querySelector(selectors.footer);
    this.subtotal = document.querySelector(selectors.subtotal);
    if (theme.features.cartAddOn) {
      this.cartAddOn = document.querySelector(selectors.cartAddOn);
    }
  },
  /**
   * Set EventBus listeners for each instance.
   */
  setEventBusListeners() {
    Frame.EventBus.listen('Toggle:cartDrawer:open', () => {
      document.body.classList.add('cart-open');
      disableBodyScroll(this.body);
    });
    Frame.EventBus.listen('Toggle:cartDrawer:close', () => {
      this.handleDrawerClose();
      window.dataLayer.push({
        event: 'GAevent',
        eventID: '18',
        eventCategory: 'cart',
        eventAction: 'minicart_interaction',
        eventLabel: 'close_minicart'
      });
    });
    Frame.EventBus.listen('AjaxCart:updated', response => this.handleDrawerOpen(response));
    Frame.EventBus.listen('AjaxCart:removingItem', item => this.setLoadingState(item));
    Frame.EventBus.listen('AjaxCart:cartEmpty', () => this.setEmptyState());
  },
  /**
   * Set listeners for datalayer events.
   */
  dataLayerLinks() {
    on('click', this.body, event => {
      if (!event.target.closest(selectors.productsLinks)) {
        return;
      }
      window.dataLayer.push({
        event: 'GAevent',
        eventID: '18',
        eventCategory: 'cart',
        eventAction: 'minicart_interaction',
        eventLabel: 'go_to_product'
      });
    });
  },
  /**
   * Instantiate quantity selectors using id.
   * - Callback has a timer to reduce number of calls.
   */
  constructQuantitySelectors() {
    const quantitySelectors = this.container.querySelectorAll(selectors.quantitySelector);
    [...quantitySelectors].forEach(item => {
      QuantitySelector("".concat(item.id), () => {
        window.clearTimeout(quantitySelectorTimer);
        quantitySelectorTimer = window.setTimeout(() => {
          this.handleQuantityUpdate(item);
        }, 250);
      }).setInitState();
    });
  },
  /**
   * Handle quantity updates.
   * @param {HTMLElement} element - Quantity selector node element.
   */
  handleQuantityUpdate(element) {
    this.setRenderingState();
    const data = {
      index: element.closest(selectors.lineItem).dataset.index,
      key: element.closest(selectors.lineItem).dataset.key,
      quantity: parseInt(element.querySelector('input').value, 10)
    };
    cart.updateItem(data.key, {
      quantity: data.quantity
    }).then(() => {
      Frame.EventBus.emit('AjaxCart:itemUpdated');
    }).catch(error => {
      if (typeof response === 'undefined') {
        return;
      }
      throw new Error('Could not update item data', error);
    });
  },
  /**
   * Conditionally handle drawer open.
   * @param {Object} data - Cart.js data.
   */
  handleDrawerOpen(data) {
    if (theme.pageType === 'cart') {
      return;
    }
    if (data.item_count === 0) {
      this.render(data);
      return;
    }
    Drawer.open(this.container);
    document.body.classList.add('cart-open');
    disableBodyScroll(this.body);
    this.setRenderingState();
    this.render(data);
  },
  /**
   * Conditionally handle drawer close.
   */
  handleDrawerClose() {
    enableBodyScroll(this.body);
    document.body.classList.remove('cart-open');
  },
  /**
   * Render line items template.
   * @param {Object} data - Cart.js data.
   */
  render(data) {
    this.setActiveState(data);
    this.constructQuantitySelectors();
    if (theme.features.cartAddOn) {
      this.handleCartAddOnState(data);
    }
    if (theme.features.currencyConverter) {
      Frame.EventBus.emit('Currency:convertAll');
    }
  },
  /**
  * Set active state.
  * @param {Object} data - Cart.js data.
  */
  setActiveState(data) {
    this.body.innerHTML = this.getActiveTemplate(data.items);
    this.subtotal.setAttribute('data-price', data.total_price);
    this.subtotal.innerHTML = formatMoney(data.total_price, theme.moneyFormat);
    this.footer.classList.remove(cssClasses.hidden);
    window.setTimeout(() => {
      this.container.classList.remove(cssClasses.loading);
      this.dataLayerLinks();
    }, 500);
  },
  /**
   * Set event delegation to remove items on container.
   * @param {HTMLElement} container - The container to apply the remove events on.
   */
  setRemoveEvents(container) {
    on('click', container, event => this.handleRemoveEvent(event));
  },
  /**
   * Handle remove events.
   * @param {Event} event - Click event.
   */
  handleRemoveEvent(event) {
    if (!this.isTargetRemove(event.target)) {
      return;
    }
    event.preventDefault();
    Frame.EventBus.emit('AjaxCart:removingItem', event.target.closest(selectors.lineItem));
    const data = {
      dataLayer: event.target.closest(selectors.lineItem).querySelector(selectors.productJson).innerHTML
    };
    cart.removeItem(event.target.dataset.key).then(response => {
      Frame.EventBus.emit('AjaxCart:itemRemoved', response);
      return response;
    }).catch(error => {
      window.console.error(error);
      return error;
    });
    window.dataLayer.push({
      event: 'removeFromCart',
      ecommerce: {
        currencyCode: window.theme.currency,
        eventPageLocator: 'minicart',
        remove: {
          actionField: {
            list: ''
          },
          products: [JSON.parse(data.dataLayer)]
        }
      }
    });
  },
  /**
   * Detect remove target click.
   * @param {HTMLElement} target - Clicked target to check remove attributes against.
   * @returns {Boolean}
   */
  isTargetRemove(target) {
    return typeof target.attributes[dataSelectors.ajaxCart] !== 'undefined' && target.attributes[dataSelectors.ajaxCart].nodeValue === 'remove';
  },
  /**
   * Set loading state.
   * @param {Object} element - DOM line item.
   */
  setLoadingState(element) {
    element.classList.add(cssClasses.removing);
  },
  /**
  * Set empty state.
  */
  setEmptyState() {
    this.body.innerHTML = this.getEmptyTemplate();
    this.footer.classList.add(cssClasses.hidden);
    if (theme.features.cartAddOn) {
      this.hideCartAddOn();
    }
  },
  /**
  * Get cart drawer body template.
  * @param {Object} items - Cart line item object data.
  * @returns {String}
  */
  getActiveTemplate(items) {
    return items.map((item, index) => {
      if (this.isAddOnItem(item)) {
        return '';
      }
      return this.getLineItemTemplate(item, index);
    }).join('');
  },
  /**
   * Get empty cart template.
   * @returns {String}
   */
  getEmptyTemplate() {
    return "<p>".concat(decodeHTMLEntities(theme.strings.cart.general.empty), "</p>");
  },
  /**
   * Get line item template.
   * @param {Object} item - Cart line item object data.
   * @param {Integer} index - Line item index.
   * @returns {string}
   */
  getLineItemTemplate(item, index) {
    return "\n      <div\n        class=\"ajax-cart__line-item\"\n        data-index=\"".concat(index, "\"\n        data-key=\"").concat(item.key, "\"\n        js-ajax-cart=\"lineItem\"\n      >\n        ").concat(this.getLineItemImage(item), "\n\n        <div class=\"ajax-cart__description\">\n          <div class=\"row no-margin no-gutter\">\n            <div class=\"col xs9 m9\">\n              <div class=\"ajax-cart__inner\">\n                ").concat(this.isVendorEnabled() ? this.getVendorTitle(item) : '', "\n\n                <a class=\"ajax-cart__title\" href=\"").concat(item.url, "\" js-ajax-cart=\"productLink\">").concat(item.product_title, "</a>\n\n                ").concat(this.getVariantTitle(item), "\n\n                <div class=\"ajax-cart__amount\">\n                  <div class=\"ajax-cart__quantity\">\n                    ").concat(item.quantity, " x&nbsp;\n                  </div>\n\n                  <div class=\"ajax-cart__price\">\n                    ").concat(this.getLineItemPrice(item), "\n                  </div>\n                </div>\n              </div>\n            </div>\n\n            <div class=\"col xs3 m3\">\n              ").concat(this.isGiftProduct(item) ? '' : this.getRemoveButton(item), "\n            </div>\n          </div>\n        </div>\n\n        <script js-product-json>\n          {\n            \"name\": \"").concat(handleize(item.product_title).replace(/-/g, '_'), "\",\n            \"id\": \"").concat(item.variant_id, "\",\n            \"price\": ").concat(item.price / 100, ",\n            \"brand\": \"alessi\",\n            \"variant\": \"").concat(item.variant_options[0] && !item.product_has_only_default_variant ? item.variant_options[0] : "", "\",\n            \"quantity\": ").concat(item.quantity, ",\n            \"dimension16\": \"").concat(item.variant_options[1] ? item.variant_options[1] : "", "\"\n          }\n        </script>\n      </div>\n    ");
  },
  /**
   * Get line item image template.
   * @param {Object} item - Variant line item object from cart.js.
   * @returns {String}
   */
  getLineItemImage(item) {
    if (!item.image) {
      return '';
    }
    return "\n    <div class=\"ajax-cart__image responsive-image__wrapper\">\n      <div class=\"ajax-cart__image-container\">\n        <img\n          alt=\"".concat(item.title, "\"\n          sizes=\"auto\"\n          src=\"").concat(getSizedImageUrl(item.image, '50x'), "\"\n          srcset=\"\n            ").concat(getSizedImageUrl(item.image, '120x'), " 120w,\n            ").concat(getSizedImageUrl(item.image, '240x'), " 240w,\n            ").concat(getSizedImageUrl(item.image, '320x'), " 320w\n          \"\n        >\n      </div>\n    </div>\n    ");
  },
  /**
   * Get variant price template.
   * - Checks if it's discounted or not.
   * @param {Object} item - Cart line item to get price from.
   * @returns {String}
   */
  getLineItemPrice(item) {
    if (item.original_line_price !== item.line_price) {
      if (item.discounted_price !== item.price) {
        return "\n          <span class=\"visually-hidden\">".concat(item.discounted_price, "</span>\n\n          <span data-price=\"").concat(item.discounted_price, "\" js-currency=\"price\">\n            ").concat(formatMoney(item.discounted_price, theme.moneyFormat), "\n          </span>\n\n          <span class=\"visually-hidden\">").concat(item.price, "</span>\n\n          <s data-price=\"").concat(item.original_price, "\" js-currency=\"compareAtPrice\">\n            ").concat(formatMoney(item.original_price, theme.moneyFormat), "\n          </s>\n\n        ");
      }
      return "\n        <span class=\"visually-hidden\">".concat(item.price, "</span>\n\n        <span data-price=\"").concat(item.price, "\" js-currency=\"price\">\n          ").concat(formatMoney(item.price, theme.moneyFormat), "\n        </span>\n\n        <span class=\"visually-hidden\">").concat(item.original_price, "</span>\n        <s>").concat(formatMoney(item.original_price, theme.moneyFormat), "</s>\n      ");
    }
    return "\n      <span data-price=\"".concat(item.price, "\" js-currency=\"price\">\n        ").concat(formatMoney(item.price, theme.moneyFormat), "\n      </span>\n    ");
  },
  /**
   * Get variant titles.
   * @param {Object} items - Cart line item object data.
   * @returns {String}
   */
  getVariantTitle(item) {
    return this.isVariantTitle(item) ? "<p class=\"ajax-cart__variant-title caption\">".concat(item.variant_title, "</p>") : '';
  },
  /**
   * Get the template for the line item remove button.
   * @param {Object} item - Cart line item object data.
   * @returns {String}
   */
  getRemoveButton(item) {
    return "\n      <button\n        class=\"ajax-cart__remove\"\n        tabindex=\"0\"\n        type=\"button\"\n        data-key=\"".concat(item.key, "\"\n        js-ajax-cart=\"remove\"\n      >\n        ").concat(theme.icons.close, "\n\n        <span class=\"visually-hidden\">\n          ").concat(theme.strings.remove, "\n        </span>\n      </button>\n    ");
  },
  /**
   * Check if the line item is a gift product.
   * @param {Object} item - Cart line item object data.
   * @returns {Boolean}
   */
  isGiftProduct(item) {
    return Boolean(item.properties && item.properties['_gift-product']);
  },
  /**
   * Check if current item is an add-on.
   * @param {Object} item - Cart variant item.
   * @returns {Boolean}
   */
  isAddOnItem(item) {
    if (!item) {
      throw Error('Could not find item');
    }
    return item.product_type.toLowerCase() === 'add-on';
  },
  /**
   * Check if vendor title setting is enabled.
   * @returns {Boolean}
   */
  isVendorEnabled() {
    return JSON.parse(this.container.dataset.enableVendorTitle);
  },
  /**
   * Get vendor title template.
   * @param {Object} item - Cart item to get vendor title from.
   * @returns {String}
   */
  getVendorTitle(item) {
    return "<p class=\"ajax-cart__vendor caption\">".concat(item.vendor, "</p>");
  },
  /**
   * Check if variant title exists.
   * @param {Object} item - Cart line item object data.
   * @returns {Boolean}
   */
  isVariantTitle(item) {
    return item.variant_options.length && !item.variant_options[0].includes('Default Title');
  },
  /**
   * Toggle on/off visibility of cart add-on note.
   * @param {Object} data - Cart data.
   */
  handleCartAddOnState(data) {
    if (!this.isAddOnInCart(data)) {
      this.hideCartAddOn();
      return;
    }
    this.showCartAddOn();
  },
  /**
   * Check if cart has an add-on product.
   * @param {Object} data - Cart data.
   * @returns {Array|Boolean}
   */
  isAddOnInCart(data) {
    return data.items.some(item => this.isAddOnItem(item));
  },
  /**
   * Show cart add-on note and re-adjust footer resize.
   */
  showCartAddOn() {
    this.cartAddOn.classList.remove(cssClasses.hidden);
    this.container.classList.add(stateSelectors.cartAddOn);
  },
  /**
   * Hide cart add-on note.
   */
  hideCartAddOn() {
    this.cartAddOn.classList.add(cssClasses.hidden);
    this.container.classList.remove(stateSelectors.cartAddOn);
  },
  /**
   * Set the rendering state of the drawer.
   */
  setRenderingState() {
    this.container.classList.add(cssClasses.loading);
  },
  /**
   * Close drawer.
   */
  close() {
    Drawer.close(this.container);
  },
  /**
   * Callback function when section is selected by
   * the Theme Editor `shopify:section:select` event.
   */
  onSelect() {
    Frame.EventBus.emit('AjaxCart:requestUpdate');
  },
  /**
   * Callback function when section is deselected by
   * the Theme Editor `shopify:section:deselect` event.
   */
  onDeselect() {
    this.close();
  },
  /**
   * Callback function when section is loaded via `sections.load()`
   * or by the Theme Editor `shopify:section:load` event.
   */
  onLoad() {
    this.init();
    this.setEventBusListeners();
  }
});