/**
 * Section: Newsletter Modal
 * ------------------------------------------------------------------------------
 * Newsletter modal configuration for complete theme support.
 * - https://github.com/Shopify/theme-scripts/tree/master/packages/theme-sections
 *
 * @namespace newsletterModal
 */
import { register } from '@shopify/theme-sections';
import Cookies from 'js-cookie';
import { on } from '~/helpers/utils';
import cssClasses from '~/helpers/cssClasses';
import Modal from '~/components/modal';

/**
 * DOM selectors.
 */
const selectors = {
  submitBtn: '[js-newsletter-modal="submit"]',
  consentCheckbox: '[js-newsletter-modal="consent"]',
  allowModal: '[js-newsletter-show="true"',
  delayShow: '[data-delay]'
};

/**
 * Register the `newsletter-modal` section type.
 * Executes on page load as well as Theme Editor `section:load` events.
 */
register('newsletter-modal', {
  /**
   * Set cached node selectors.
   */
  setNodeSelectors() {
    this.submitBtn = document.querySelector(selectors.submitBtn);
    this.consentCheckbox = document.querySelector(selectors.consentCheckbox);
    this.allowModal = document.querySelector(selectors.allowModal);
    this.delayShow = document.querySelector(selectors.delayShow).dataset.delay;
  },
  /**
   * Initialise section objects.
   */
  init() {
    this.settings = {
      id: 'newsletter-modal',
      namespace: 'newsletter_modal',
      iubenda_consent: '_iub_cs',
      geolocation: 'redirect_geolocation'
    };
    this.eventSelectors = {
      modalClose: "Modal:Close:".concat(this.settings.id)
    };
    this.modal = Modal({
      id: this.settings.id
    });
  },
  /**
   * Set event listeners on component.
   */
  setEventBusListeners() {
    on('change', this.consentCheckbox, event => {
      if (event.target.checked) {
        this.submitBtn.removeAttribute('disabled');
      } else {
        this.submitBtn.setAttribute('disabled', true);
      }
    });
    Frame.EventBus.listen(this.eventSelectors.modalClose, () => this.handleCloseEvents());
  },
  /**
   * Handle close callback function by setting cookie.
   */
  handleCloseEvents() {
    Cookies.set(this.settings.namespace, 'dismissed', {
      expires: 7
    });
  },
  /**
   * Check if a Cypress test is being run.
   * @returns {Boolean}
   */
  isTestCondition() {
    if (window.Cypress) {
      this.handleCloseEvents();
      return true;
    }
    return false;
  },
  /**
   * Get all modal elements currently on the DOM.
   * @returns {Array}
   */
  getAllActiveModals() {
    return Array.from(document.querySelectorAll(".modal.".concat(cssClasses.active, ", .meganav-open, .js-search-active")));
  },
  /**
   * Callback function when section is loaded via the `sections.load()`
   * function or by Theme Editor `shopify:section:unload` event.
   */
  onLoad() {
    this.init();
    this.setNodeSelectors();
    this.setEventBusListeners();
    this.modal.init();
    var userAcceptedIubConsent = document.cookie.indexOf(this.settings.iubenda_consent) != -1;

    /**
     * Set conditions on when to show the modal.
     */
    if (!Cookies.get(this.settings.namespace) && userAcceptedIubConsent && Cookies.get(this.settings.geolocation) && this.allowModal && !this.isTestCondition()) {
      window.setTimeout(() => {
        if (this.getAllActiveModals().length !== 0) {
          return;
        }
        this.modal.show(this.settings.id);
      }, this.delayShow);
      console.log(this.delayShow);
    }
  },
  /**
   * Callback function when section is unloaded by the
   * Theme Editor `shopify:section:unload` event.
   */
  onUnload() {
    this.modal.close(this.settings.id);
  },
  /**
   * Callback function when section is selected by the
   * Theme Editor `shopify:section:select` event.
   */
  onSelect() {
    this.modal.show(this.settings.id);
  },
  /**
   * Callback function when section is deselected by the
   * Theme Editor `shopify:section:deselect` event.
   */
  onDeselect() {
    this.modal.close();
  }
});